/**
 * 
 */
function userLogout() {
  const url = '/user/logout.do';
  return post(url).then(() => {
    removeUserDataStorage();
    emitUserDataEvent('logout');
    return '';
  });
}

/**
 * 
 */
function requestUserList(pageSize, pageNum) {
  const url = '/manage/user/list.do';
  return get(url, { pageSize, pageNum });
}

export {
  requestUserLogin,
  userLogout,
  requestUserList,
};
