import React from 'react';
import PropTypes from 'prop-types';
import { Table } from 'antd';

import { userRoute } from 'util/route.js';
import PageWrapper from 'component/page-wrapper';

class UserList extends React.Component {
  componentDidMount() {
    document.title = '';
    this.props.getUserList(10, 1);
  }

  render() {
    const { userListData, pageSize, pageNumber, total } = this.props.userListData;

    const columns = [{
      title: '',
      dataIndex: 'username',
      key: 'username',
    }, {
      title: '',
      dataIndex: 'phone',
      key: 'phone',
    }, {
      title: '',
      dataIndex: 'email',
      key: 'email',
    }, {
      title: '',
      dataIndex: 'createTime',
      key: 'createTime',
      render: text => new Date(text).toLocaleDateString()
    }, {
      title: 'Action',
      key: 'action',
    }];

    const tableProps = {
      columns,
      dataSource: userListData,
      rowKey: 'id',
      pagination: {
        pageSize,
        current: pageNumber,
        total,
        onChange: (page, pageSize) => this.props.getUserList(pageSize, page)
      }
    };

    const routeData = [{
      key: userRoute.list,
      text: ''
    }];

    return (
      <PageWrapper routeData={routeData}>
        <Table {...tableProps} />
      </PageWrapper>
    );
  }
}

UserList.propTypes = {
  userListData: PropTypes.object.isRequired,
  getUserList: PropTypes.func.isRequired,
};

export default UserList; 
