// 
import React from 'react';
import { Row, Col, Button, Modal, Form, Select } from 'antd';
import PropTypes from 'prop-types';

import { productRoute, userRoute, orderRoute } from 'util/route.js';
import PageWrapper from 'component/page-wrapper';
import Card from './card.js';
import style from './style.scss';
const weeks = ['', '', '', '', '', '', '']
const FormItem = Form.Item;
const { Option } = Select;
const AddModal = Form.create()((props) => {
  const {
    showEditModal,
    onAddAttr,
    onClose,
    form,
    jiaoLianList = [],
  } = props;
  const { getFieldDecorator } = form;
  return (
    <Modal
      title=""
      visible={showEditModal}
      onOk={onAddAttr}
      onCancel={onClose}
      okText=""
      cancelText=""
    >
      <Form layout="inline">
        <FormItem label="">
          {getFieldDecorator('jiaolian', {
            // initialValue: '',
          })(
            <Select
              style={{ width: 200 }}
              placeholder=""
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) => {
                return option.props.children.indexOf(input) != -1;
              }}
            >
              {(jiaoLianList && jiaoLianList).map((item) => {
                return (
                  <Option
                    value={item.id}
                  >{`${item.name}`}</Option>
                );
              })}
            </Select>
          )}
        </FormItem>
        <FormItem label="">
          {getFieldDecorator('cd')(
            <Select
              style={{ width: 200 }}
              showSearch
              placeholder=""
              optionFilterProp="children"
              filterOption={(input, option) => {
                return option.props.children.indexOf(input) != -1;
              }}
            >
              {(jiaoLianList && jiaoLianList).map((item) => {
                return (
                  <Option
                    value={item.id}
                  >{`${item.name}`}</Option>
                );
              })}
            </Select>
          )}
        </FormItem>
      </Form>
    </Modal>
  );
});


class Home extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      visible: false
    }
  }

  componentDidMount() {
    document.title = '';
    this.props.getStatisticData();
    this.props.getJiaoLianList();
  }

  saveFormRef = (form) => {
    this.form = form;
  };

  showAddModal = () => {
    this.setState({
      visible: true
    })
  }

  handleOk = () => {

  }

  handleCancel = () => {
    this.setState({
      visible: false
    })
  }

  render() {
    const { home } = this.props;
    const { statisticData, jiaoLianList } = home;
    const { userCount, orderCount, productCount } = statisticData;

    return (
      <PageWrapper showBackgroundColor={false}>
        <Row gutter={40}>
          <Col span={6}>
            <Card title='' num={userCount} linkTo={userRoute.list} />
          </Col>
          <Col span={6}>
            <Card title='' num={productCount} linkTo={productRoute.list} />
          </Col>
          <Col span={6}>
            <Card title='' num={orderCount} linkTo={orderRoute.list} />
          </Col>
          <Col span={6}>
            <Card title='' num={orderCount} linkTo={orderRoute.list} />
          </Col>
        </Row>
        <div className={style.weekBox}>
          <div className={style.titleBox}>
            <div className={style.weekBoxTitle}></div>
            <Button type="primary" onClick={this.showAddModal} className={style.weekBtn}></Button>
          </div>
          <div className={style.kcBox}>
            {
              [1,2,3,4,5,6,7].map(item => {
                return (
                  <div key={item} className={style.weekdayItemBox}>
                    <div className={style.weekday}>
                        10/2
                    </div>
                    <div className={style.weekdayKc}>
                        09:30-11:00 
                    </div>
                  </div>
                )
              })
            }
          </div>
        </div>
        <div className={style.weekBox}>
          <div className={style.weekBoxTitle}></div>
          <div className={style.cdBox}>
            {
              [1,2,3].map(item => {
                return (
                  <div key={item} className={style.cdItemCard}>
                    <div className={style.cdName}>1</div>
                    <div className={style.cdDate}>: 14:00-16:00</div>
                    <div className={style.cdFZR}>xxx</div>
                    <div className={`${style.cdType} ${item == 1 && style.cdType2}`}>{item == 1 ? '' : ''}</div>
                  </div>
                )
              })
            }
          </div>
        </div>
        {/* <Modal
          title=""
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >

        </Modal> */}

        <AddModal
          showEditModal={this.state.visible}
          ref={this.saveFormRef}
          onAddAttr={this.handleOk}
          onClose={this.handleCancel}
          jiaoLianList={jiaoLianList}
        />
      </PageWrapper>
    );
  }
}

Home.propTypes = {
  home: PropTypes.object.isRequired,
  getStatisticData: PropTypes.func.isRequired,
};

export default Home;
