import axios from 'axios';

import { removeUserDataStorage } from './storege';

const request = (config) => {
  const { method, url, data, params } = config;

  const axioConfig = {
    method,
    url: `${url}`,
    data,
    params,
  };

  return axios(axioConfig).then(response => {
    const { status, data, msg } = response.data;

    if (status === 0) {
      return data;
    } else if (status === 10) {
      // status  10 
      removeUserDataStorage();
      return Promise.reject(msg);
    } else {
      return Promise.reject(msg);
    }
  });
};

/**
 * get 
 * @param {string} url 
 * @param {object} params 
 */
const get = (url, params) => {
  return request({
    method: 'GET',
    url,
    params,
  });
};

/**
 * 
 * @param {object} data 
 */
const createFormData = data => {
  if (!data) return;

  let processedData = new FormData();
  Object.keys(data).forEach(element => {
    processedData.append(element, data[element]);
  });

  return processedData;
};

/**
 * post 
 * @param {string} url 
 * @param {object} data 
 */
const post = (url, data) => {
  return request({
    method: 'POST',
    url,
    data: createFormData(data),
  });
};

export {
  get,
  post
};
